import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

function getWorkoutColor(workout) {
	switch (workout.type) {
		case 1:
			return 'border-primary';	
		case 2:
			return 'border-danger';
		case 3:
			return 'border-success';
		default:
			break;
	}
}

const initialState = {};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadAll();	
    }

	async loadAll() {
		const { appState, userStore } = this.props.stores;
        appState.getFingerprint();
        await userStore.getInfo();
    }

	render() {
		const { userStore } = this.props.stores;
		const { user, team, dogs, workouts, workoutSummary } = userStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
				<div class='row'>
					<div class='col-12 mb-4'>
						<h5 class='mt-4'>user</h5>
						{JSON.stringify(user)}
						<h5 class='mt-4'>team</h5>
						{JSON.stringify(team)}
					</div>
				</div>
				<div class='row'>
					<div class='col-12 mb-4'>
						<h5>Hundene - {dogs.length} stk</h5>
						<div class='row'>
							{dogs && dogs.map(dog => (
								<div class='col-6 col-sm-4 col-lg-2 mt-2'>
									{/* {JSON.stringify(dog, null, 4)} */}
									<div class='text-center border p-2 mb-1'>
										<a href={`/dogs/${dog.id}`} class='stretched-link'>
											{dog.image ? <img src={dog.image.s3SmallLink} class='img-fluid' style='max-height: 13vh;' /> : <i class='fas fa-dog text-muted' style='font-size: 4vw;' />}
										</a>
									</div>
									<div>
										<a href={`/dogs/${dog.id}`}>
											{dog.name}
										</a> <span class='float-right'>
											{dog.birth && <span class='mr-2'>{util.age(dog.birth, dog.deceased)} Ã¥r</span>}
											{dog.gender === 'female' ? <i class='fas fa-venus' /> : <i class='fas fa-mars' />}
										</span>
									</div>
									<small>
										<i>{dog.shortname}</i><br />
										{dog.birth && <span>{util.isoDate(dog.birth, false, false, true)}<br /></span>}
										{dog.chipdId && <small>{dog.chipId}</small>}
									</small>
								</div>
							))}
						</div>
					</div>
				</div>
				<div class='row'>
					<div class='col-12 mb-4'>				
						<h5>Treningsturene - {workouts.length} stk</h5>
						<div class='row'>
							{workouts && workouts.map(workout => (
								<div class={`col-6 col-sm-4 col-md-3 col-lg-2 mt-2 p-2`}>
									{/* {JSON.stringify(workout, null, 4)} */}
									<div class={`text-center border ${getWorkoutColor(workout)} rounded p-2 mb-1`}>
										<a href={`/workouts/${workout.id}`} class='stretched-link'>
											{workout.image ? <img src={workout.image} class='img-fluid' /> : <i class='fas fa-running text-muted' style='font-size: 4vw;' />}
										</a>
									</div>
									<div style='overflow: hidden; white-space: nowrap;'>
										{workout.name}
									</div>
									<div>
										<small class='float-right text-muted'>
											{util.isoDate(workout.date)}
										</small>
										<br />
									</div>
									<div>
										<small>
											<span class='text-muted'>
												<i class='fas fa-road' /> {workout.distanceKm}km
												<i class='fas fa-mountain ml-2' /> {workout.elevation}m
												{workout.dogs && <span><i class='fas fa-dog ml-2' /> {workout.dogs.length}</span>}
											</span>
											<i>{workout.url}</i><br />
										</small>
									</div>
								</div>
							))}
						</div>
					</div>
				</div>
				<div class='row'>
					<div class='col-12 mb-4'>	
						<h5 class='mt-4'>workoutSummary</h5>
						{JSON.stringify(workoutSummary)}
					</div>
				</div>
			</div>
		);
	}
}

export default Home;
